import React, {Component} from 'react';
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';
// var flatten = require('flat')
import { Field, reduxForm, initialize, formValueSelector } from 'redux-form'
import * as actions from '../../actions';

const hears = ['Word of Mouth', 'Facebook Ads', 'Family Members', 'School Staff', 'Others']

class EditStudentProfileForm extends Component {

  handleInitialize(user) {
    const email = localStorage.getItem('email')
    const initData = {
      ...user,
      email
    };   
    this.props.initialize(initData);
  }

  componentDidMount(){  
    const user = this.props.userProfile;
    console.log(user)
    this.handleInitialize(user);
  }

  render(){
    const { handleSubmit, fieldDisabled, load, pristine, reset, submitting } = this.props

    const radioLabelMargins = {
      marginRight: '10px',
    }
    const radioButtonMargins = {
      marginRight: '10px',
    }   
    

    if (!this.props.userProfile ) {
      return (
        <div>loading...</div>
      )
    } else {
      const user = this.props.userProfile
      console.log(this.props)
      return (
        <form onSubmit={handleSubmit} >

          <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">First Name *</label>
          </div>
          <div className="field-body">
            <div className="field is-normal">
              <p className="control is-expanded has-icons-left">
                <Field name="firstName" component="input" type="text" placeholder="First Name" className='input'/>
                <span className="icon is-small is-left">
                  <i className="fa fa-user"></i>
                </span>
              </p>
            </div>
          </div>
        </div>

          <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Last Name *</label>
          </div>
          <div className="field-body">
            <div className="field is-narrow">
              <p className="control is-expanded has-icons-left">
                <Field name="lastName" component="input" type="text" placeholder="Last Name" className='input'/>
                <span className="icon is-small is-left">
                  <i className="fa fa-user"></i>
                </span>
              </p>
            </div>
          </div>
        </div>

        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Date of Birth *</label>
          </div>
          <div className="field-body">
            <div className="field is-narrow">
                <Field name="dob" component="input" type="date" placeholder="Date of Birth" className="input" />
            </div>
          </div>
        </div>
        
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Email *</label>
          </div>
          <div className="field-body">
            <div className="field is-narrow">
              <p className="control is-expanded has-icons-left has-icons-right">
                  <input className="input is-success" type="email" placeholder="Email" />
                  <span className="icon is-small is-left">
                    <i className="fa fa-envelope"></i>
                  </span>
                  <span className="icon is-small is-right">
                    <i className="fa fa-check"></i>
                  </span>
                </p>
            </div>
          </div>
        </div>

        <div className="field is-horizontal">
          <div className="field-label">
            <label className="label">Mobile Number</label>
          </div>
          <div className="field-body">
            <div className="field is-expanded">
              <div className="field has-addons">
                <p className="control">
                  <a className="button is-static">
                    +852
                  </a>
                </p>
                <p className="control is-expanded">
                  <input className="input" type="tel" placeholder="Your phone number" />
                </p>
              </div>
              <p className="help">Do not enter the first zero</p>
            </div>
          </div>
        </div>
        


        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Department</label>
          </div>
          <div className="field-body">
            <div className="field is-narrow">
              <div className="control">
                <div className="select is-fullwidth">
                  <select>
                    <option>Business development</option>
                    <option>Marketing</option>
                    <option>Sales</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>


        
        <div className="field is-horizontal">
          <div className="field-label">
            <label className="label">Already a member?</label>
          </div>
          <div className="field-body">
            <div className="field is-narrow">
              <div className="control">
                <label className="radio">
                  <input type="radio" name="member" />
                   Yes
                </label>
                <label className="radio">
                  <input type="radio" name="member" />
                   No
                </label>
              </div>
            </div>
          </div>
        </div>
        
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Subject</label>
          </div>
          <div className="field-body">
            <div className="field">
              <div className="control">
                <input className="input is-danger" type="text" placeholder="e.g. Partnership opportunity" />
              </div>
              <p className="help is-danger">
                This field is required
              </p>
            </div>
          </div>
        </div>
        
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Question</label>
          </div>
          <div className="field-body">
            <div className="field">
              <div className="control">
                <textarea className="textarea" placeholder="Explain how we can help you"></textarea>
              </div>
            </div>
          </div>
        </div>
        
        <div className="field is-horizontal">
          <div className="field-label">
          </div>
          <div className="field-body">
            <div className="field">
              <div className="control">


                <button type="submit" className='button is-primary' style={radioButtonMargins}>Submit</button>
                <button type="button" disabled={pristine || submitting} onClick={reset} className='btn button is-danger'>Reset Form</button>
                
              </div>
            </div>
          </div>
        </div>
      </form>

      )
    }
  }
}


const selector = formValueSelector('editStudentProfile')


const form = reduxForm({  
  form: 'editStudentProfile'
});

export default form(EditStudentProfileForm);  




